// 店铺动态相关接口
import axios from './../fetch'

// 店铺动态列表
export const shopDynamicList = params => { return axios.get('/admin/store/storeDynamic/page', { params: params }) }

// 删除店铺动态
export const shopDynamicDelete = params => { return axios.post('/admin/store/storeDynamic/delete', params) }

// 店铺动态修改
export const shopDynamicUpdate = params => { return axios.post('/admin/store/storeDynamic/update', params) }
// 店铺动态新增
export const shopDynamicSave = params => { return axios.post('/admin/store/storeDynamic/save', params) }
// 店铺动态
export const shopDynamicGet = params => { return axios.get('/admin/store/storeDynamic/get', { params: params }) }



