<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/shopDynamic/list" @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt >
        <li>带 * 为必填项</li>
      </cms-prompt>
      <el-form ref="form" :rules="saveRules" :model="formData" label-width="210px">
        <el-form-item label="动态内容" prop="content">
          <el-input v-model="formData.content" type="textarea" :rows="3"></el-input>
          <div class="gray">仅限140字以内</div>
        </el-form-item>
        <el-form-item label="是否显示" prop="isShow">
          <cms-switch v-model="formData.isShow"></cms-switch>
        </el-form-item>
        <el-form-item label="是否置顶" prop="isTop">
          <cms-switch v-model="formData.isTop"></cms-switch>
          <div class="gray">设置是，信息将以跑马灯的形式循环显示在店铺首页</div>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="update" v-color v-if="$route.query.type=='update'">保存</cms-button>
         <cms-button  @click="save(false)" v-color  v-if="$route.query.type=='save'">保存并新增</cms-button>
        <cms-button @click="save(true)"  v-color  v-if="$route.query.type=='save'">保存</cms-button>
        <cms-button type='rest' @click="rest">重置</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { shopDynamicSave, shopDynamicUpdate, shopDynamicGet } from './../../../../api/shop/shopDynamic.js'
var defaultData = {
  content: '',
  isShow: true,
  isTop: false
}
export default {
  mixins: [form],
  data() {
    return {
      formData: Object.assign({}, defaultData),
      saveRules: {
        // 新增规则
        content: [rules.required('请填写动态'), rules.len(1, 140)]
      }
    }
  },
  methods: {
    getFormData(id) {
      // 通过id获取表单信息
      shopDynamicGet({ id: id }).then(res => {
        this.formData = Object.assign(defaultData, res.data)
      })
    },
    save(state) { // 添加
      this.saveForm(shopDynamicSave, this.formData, state, '/shopDynamic/list', 'list')
    },
    update() {
      this.updateForm(shopDynamicUpdate, this.formData, '/shopDynamic/list', 'list')
    }
  },
  created() {
    if (this.$route.query.type === 'update') {
      this.getFormData(this.$route.query.id)
    }
  }
}
</script>



